.flap {
  box-sizing: border-box;
  position: absolute;
  bottom: -1vmin;
  left: -1vmin;
  height: 65vmin;
  width: 60vmin;
  border-bottom-left-radius: 5vmin;
  border-bottom-right-radius: 5vmin;
  border: 1vmin solid rgb(88, 72, 74);
  background-color: rgb(232, 72, 72);
  transform-origin: right;
  transition: transform 300ms linear;
  z-index: 50;
/*   transform: rotateY(180deg); */
}

.inside-of-flap {
  opacity: 0;
}

.trapezoid {
  box-sizing: border-box;
  position: absolute;
  height: 9vmin;
  width: 20vmin;
  right: -1vmin;
  top: -8vmin;
  border-top: .8vmin solid rgb(88, 72, 74);
  border-right: 1vmin solid rgb(88, 72, 74);
  background-color: rgb(232, 72, 72);
}

.diagonal {
  box-sizing: border-box;
  position: absolute;
  height: 9vmin;
  width: 20vmin;
  right: 12.9vmin;
  transform: rotate(-45deg);
  top: -2.2vmin;
  border-top: 1vmin solid rgb(88, 72, 74);
  background-color: rgb(232, 72, 72);
}

.cover {
  box-sizing: border-box;
  position: absolute;
  height: 10vmin;
  width: 100%;
  background-color: rgb(232, 72, 72);
  top: -.1vmin;
  left: 0;
  z-index: 5;
}

