  width: 70%;
  height: 18%;
  left: 50%;
  top: 12.5%;
  transform: translate(-50%, 0);
  background-color: rgb(102, 102, 102);
  box-sizing: border-box;
  border: 1vmin solid var(--grey);
  border-radius: 1vmin;
  position: absolute;
  z-index: 100;
  overflow: hidden;
}

.inside-black-screen .shadow {
  position: absolute;
  width: 100%;
  height: 70%;
  border-radius: 50%;
  background-color: rgb(92, 92, 92);
  top: 85%;
}

