  height: 2vmin;
  width: 7vmin;
  border-radius: 1vmin;
  border: .5vmin solid var(--grey);
  background-color: rgb(102, 102, 102);
  overflow: hidden;
  z-index: 10;
}

.small-black-button-1 {
  top: 57.5%;
  left: 15%;
}

.small-black-button-2 {
  top: 57.5%;
  left: 30%;
}

.small-black-button .shadow {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 70%;
  background-color: rgb(76, 79, 79);
  border-radius: 50%;
}

.grey-button-container {
  width: 28%;
  height: 9%;
  right: 15%;
  top: 65%;
  box-sizing: border-box;
  border: .7vmin solid var(--grey);
  border-radius: 1vmin;
  position: absolute;
